<template>
    <div>
        <NavHead></NavHead>
        <div class="bg">
            <div
                class="list-card"
                v-for="(item, index) in booklist"
                :key="index"
                @click="
                    $router.push({
                        name: 'ListDetail',
                        params: { id: item._id },
                    })
                "
            >
                <div class="card-content">
                    <div class="content-title">{{ item.title }}</div>
                    <p class="content-text">{{ item.desc }}</p>
                    <div class="content-others">
                        <span class="book-count">{{ item.bookCount }}本</span>
                        <span class="point"></span>
                        <span class="book-collection"
                            >{{ item.collectorCount }}收藏</span
                        >
                        <span class="point"></span>
                        <span class="book-readed">{{
                            changeCount(item.readCount)
                        }}</span>
                    </div>
                </div>
                <div class="card-img">
                    <img
                        v-for="(img, index) in item.covers"
                        :key="index"
                        :src="'http://statics.zhuishushenqi.com' + img"
                    />
                </div>
            </div>
            <div class="empty" v-if="showEmpty">没有更多书单了...</div>
            <div class="more" @click="getMore" v-else>点击加载更多...</div>
        </div>
    </div>
</template>

<script>
import NavHead from "@/components/NavHead.vue";
import { getData } from "@/api/api.js";
import store from "@/store/index.js";
export default {
    name: "BookList",
    components: {
        NavHead,
    },
    data() {
        return {
            start: 0,
            booklist: [],
            duration: "",
            sort: "",
            showEmpty: false,
        };
    },
    methods: {
        changeCount(val) {
            return (val / 10000).toFixed(1) + "万阅读";
        },
        getMore() {
            this.start += 1;
            store.state.loading = true;
            getData("http://novel.kele8.cn/booklists", {
                sort: this.sort,
                duration: this.duration,
                start: this.start,
            })
                .then((res) => {
                    if (res.data.bookLists) {
                        this.booklist = [
                            ...this.booklist,
                            ...res.data.bookLists,
                        ];
                    } else {
                        this.showEmpty = true;
                    }

                    store.state.loading = false;
                })
                .catch((err) => {
                    console.log(err);
                    store.state.loading = false;
                });
        },
    },
    beforeRouteEnter(to, from, next) {
        let sort = "collectorCount";
        let duration = "last-seven-days";
        let start = 0;
        let booklist = [];
        let listData = JSON.parse(localStorage.getItem("booklist"));
        if (listData && listData.expires > Date.now()) {
            booklist = listData.data.bookLists;
            next((vm) => {
                vm.sort = sort;
                vm.duration = duration;
                vm.start = start;
                vm.booklist = booklist;
                vm.showEmpty = false;
            });
        } else {
            store.state.loading = true;
            getData("http://novel.kele8.cn/booklists", {
                sort: sort,
                duration: duration,
                start: start,
            })
                .then((res) => {
                    localStorage.setItem(
                        "booklist",
                        JSON.stringify({
                            expires: Date.now() + 24 * 60 * 60 * 1000,
                            data: res.data,
                        })
                    );
                    booklist = res.data.bookLists;
                    store.state.loading = false;
                    next((vm) => {
                        vm.sort = sort;
                        vm.duration = duration;
                        vm.start = start;
                        vm.booklist = booklist;
                        vm.showEmpty = false;
                    });
                })
                .catch((err) => {
                    console.log(err);
                    store.state.loading = false;
                });
        }
    },
};
</script>

<style lang="less" scoped>
.bg {
    max-height: 84vh;
    overflow-y: scroll;
    background-color: #f4f4f4;
    .list-card {
        background-color: #fff;
        margin-bottom: 10px;
        display: flex;
        padding: 10px;
        .card-content {
            flex: 7;
            text-align: left;
            .content-title {
                max-width: 95%;
                font-size: 16px;
                font-weight: bold;
                padding-left: 5px;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 1;
                text-overflow: ellipsis;
                overflow: hidden;
            }
            .content-text {
                font-size: 14px;
                color: #999;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
                text-overflow: ellipsis;
                overflow: hidden;
            }
            .content-others {
                display: flex;
                align-items: center;
                font-size: 13px;
                color: #a09fa5;
                .point {
                    display: inline-block;
                    vertical-align: middle;
                    width: 4px;
                    height: 4px;
                    background-color: #a09fa5;
                    border-radius: 50%;
                    margin: 0 5px;
                }
            }
        }
        .card-img {
            flex: 5;
            margin-left: 15px;
            width: 100%;
            position: relative;
            img {
                position: absolute;
                bottom: 5px;
            }
            img:nth-child(1) {
                width: 40%;
                left: 0;
                z-index: 1;
            }
            img:nth-child(2) {
                width: 45%;
                left: 30px;
                z-index: 5;
            }
            img:nth-child(3) {
                width: 40%;
                right: 0;
                z-index: 1;
            }
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
    .empty,
    .more {
        padding: 5px;
    }
}
</style>
